import React, { useState } from "react";
import styles from "./ResultList.module.css";
import ResultMovieItem from "./ResultMovieItem";
import MovieDetail from "./MovieDetail";
const ResultList = (props) => {
  //Hin th ni dung chung ca result List nu c li v nu khng c movie tm thy
  let content = "Please input keyword to search";
  if (props.error) {
    content = props.error;
  }
  //Hin th ni dung loading khi ang fetching data search
  if (props.isLoading) {
    content = "Loading...";
  }
  console.log(props.dataSearch);
  //Nu nh kt kt qu tm kim truyn xung  render l array khc rng th display list phim cn khng th hin th message khng c phim
  const displayResult =
    props.dataSearch.length > 0 ? (
      props.dataSearch.map((movie, i) => (
        <ResultMovieItem key={i + 1} moviedata={movie}></ResultMovieItem>
      ))
    ) : (
      <p className={styles.noMovie}>{content}</p>
    );
  //JSX tr ra tiu  kt qu search, v list cc phim tm thy c
  // nu nh khng c data phim no tm c th s hin th message yu cu nhp keyword ng
  // V phn moviedetail khi ngi dng nhn vo phim bt k
  return (
    <div className={`${styles.container} movieCheck`}>
      <h2>Search Result</h2>
      <div
        className={` ${styles.resultList}
          ${!props.dataSearch.length && styles.noList}`}
      >
        {displayResult}
      </div>
      {props.movieModaleOpen && props.curMovieID && (
        <MovieDetail
          movieTrailer={props.movieTrailer}
          error={props.error}
          moviedetail={props.dataSearch.filter((movie) => {
            return movie.id === Number(props.curMovieID);
          })}
        />
      )}
    </div>
  );
};
export default ResultList;
