import React from "react";
import styles from "./Banner.module.css";
const Banner = (props) => {
  //t cc gi tr hin th  banner, khi cha c gi tr fetch s l rng mc nh khi data load xong th s thnh data hin th
  //name, overview, background
  const movieName = props.movieBannerRandom
    ? props.movieBannerRandom.original_name
    : "";
  const movieOverview = props.movieBannerRandom
    ? props.movieBannerRandom.overview
    : "";
  // Background ca banner s l mu en cho n khi props bannerrandom c truyn xung s thay bng backdrop-path
  const backgroundImage = props.movieBannerRandom
    ? `url(https://image.tmdb.org/t/p/original${props.movieBannerRandom.backdrop_path})`
    : "black";
  //banner tr ra cc thnh phn theo nh mu, thng tin phim, background, cc nt bm, khi cha load data xong s hin th loading
  return (
    <div
      className={styles.container}
      style={{
        backgroundImage: backgroundImage,
      }}
    >
      <div
        className={`${styles.banner} ${props.isLoading ? styles.loading : ""}`}
      >
        {props.isLoading ? (
          <h2>Loading...</h2>
        ) : (
          <div className={styles.movieIntro}>
            <h2>{movieName}</h2>
            <button>Play</button>
            <button>My List</button>
            <p>{movieOverview}</p>
          </div>
        )}
      </div>
    </div>
  );
};
export default Banner;
