import React from "react";
import styles from "./MovieDetail.module.css";
import YouTube from "react-youtube";
const MovieDetail = (props) => {
  // Thuc tnh opts  s dng element Youtube t react-youtube
  const opts = {
    height: "400",
    width: "100%",
    playerVars: {
      autoplay: 0,
    },
  };
  // To bin imageBackup s c s dng trong trng hp khng c trailer or teaser t youtube
  const imageBackup =
    props.moviedetail[0].backdrop_path ?? props.moviedetail[0].poster_path;
  // JSX ca component ny s hin th cc thng tin chi tit ca b phim khi ta click vo, name, title, rate, release date, trailer
  //Nu nh props cha thng tin trailer truyn xung c length khc 0 s hin th video cn k hin th hnh nh
  //Nu khng c hnh nh backup s hin th nh mc nh "Poster not found"
  return (
    <div className={styles.movieDetailContainer}>
      <div className={styles.movieDetailContent}>
        <h2>
          {props.moviedetail[0].original_title ??
            props.moviedetail[0].original_name}
        </h2>
        <h4>
          Release Date:{" "}
          {props.moviedetail[0].release_date ??
            props.moviedetail[0].first_air_date}
        </h4>
        <h4>Vote: {props.moviedetail[0].vote_average}/10</h4>
        <p>{props.moviedetail[0].overview}</p>
        {props.error && <h3>{props.error}</h3>}
      </div>
      <div className={styles.movieTrailerFrame}>
        {props.movieTrailer.length !== 0 && props.movieTrailer ? (
          <YouTube videoId={props.movieTrailer.key} opts={opts} />
        ) : (
          <img
            src={
              imageBackup
                ? `https://image.tmdb.org/t/p/original${imageBackup}`
                : "/ti xung.png"
            }
          />
        )}
      </div>
    </div>
  );
};
export default MovieDetail;
