import React from "react";
import MovieListItem from "./MovieListItem";
import styles from "./MovieList.module.css";
const MovieList = (props) => {
  //JSX tr ra cc list movie, v trong props. movieDataArr fetch t trang ch browse c 1 API search khng dng n nn slice array t 0 n -1
  //Map cc phn t array thnh cc list phim ri truyn data xung MovieListItem
  return (
    <div className={styles.container}>
      {
        <div className={styles.movieList}>
          {props.movieDataArr.map((movieListArr, index) => {
            return (
              <MovieListItem
                key={index + 1}
                movieListArr={movieListArr}
                isLoading={props.isLoading}
              />
            );
          })}
        </div>
      }
    </div>
  );
};
export default MovieList;
