import React, { useState } from "react";
import MovieItem from "./MovieItem";
import styles from "./MovieListItem.module.css";
import MovieDetail from "./MovieDetail";
const MovieListItem = (props) => {
  //t cc state  qun l1y vic hin th moviedetail, ID ca phim khi c click vo, trailer v error
  const [movieModaleOpen, setMovieModalOpen] = useState(false);
  const [curMovieID, setCurMovieID] = useState(0);
  const [movieTrailer, setMovieTrailer] = useState([]);
  const [error, setError] = useState("");
  const token = "8qlOkxz4wq";
  const userId = "User 01";
  //t tn cc th loi phim theo type lu t props movieListArr  hin th  mc tng dng phim
  let titleList;
  if (props.movieListArr.type === "fetchNetflixOriginals") {
    titleList = "";
  } else if (props.movieListArr.type === "fetchTrending") {
    titleList = "Xu Hng";
  } else if (props.movieListArr.type === "fetchTopRated") {
    titleList = "Xp hng cao";
  } else if (props.movieListArr.type === "fetchActionMovies") {
    titleList = "Hnh ng";
  } else if (props.movieListArr.type === "fetchComedyMovies") {
    titleList = "Hi";
  } else if (props.movieListArr.type === "fetchHorrorMovies") {
    titleList = "Kinh D";
  } else if (props.movieListArr.type === "fetchRomanceMovies") {
    titleList = "Lng mng";
  } else if (props.movieListArr.type === "fetchDocumentaries") {
    titleList = "Ti liu";
  }
  //Xy dng function ly trailer ca phim

  const getTrailer = async function (movieId) {
    try {
      //Nu titleList l "" th  l type originalNetflix fetch t themoviesdb cn li s ly t backend
      const response =
        titleList === ""
          ? await fetch(
              `https://api.themoviedb.org/3/movie/${movieId}/videos?api_key=381d90cbe832f9ed337e1407f33d8116`
            )
          : await fetch(
              `${process.env.REACT_APP_BACKEND_URL}/api/movies/video?token=${token}&userId=${userId}`,
              {
                method: "POST",
                mode: "cors",
                headers: { "Content-Type": "application/json" },
                body: JSON.stringify({ movieId: movieId }),
              }
            );
      console.log(response);
      //Trong trng hp API b li khng tn ti th bo vi user l phim k c trailer
      //----on code di y l logic x l cho c trailer ly t backend
      if (!response.ok) {
        const errorMessage = await response.json();
        console.log(errorMessage);
        //Mt l tr ra message li t backend, hai nu ly video t themoviesdb b li s hin th Not found Video
        throw new Error(errorMessage.message ?? "Not found Video");
      }
      const data = await response.json();

      //i vi nhng trailer c ly t backend  c x l logic  ly ra Trailer mi nht nn tr v mng ch 1 phn t
      //Nhng trailer ly t themoviesdb s phi c x l logic li bng khi else, data.length === 0  c x l  backend
      if (titleList !== "") {
        setMovieTrailer(data[0]);
        setError("");

        //---------on code ny x l cho trailer fetch t themoviesdb cho phn nextflixOriginal
      } else {
        // Lc ra ly data trailer theo th t u tin ly trailer nu khng c mi ly teaser vi site Youtube, nu khng c tr mng rng
        let trailerdata = data.results.filter((arr) => {
          return arr.site === "YouTube" && arr.type === "Trailer";
        });
        if (trailerdata.length === 0) {
          trailerdata = data.results.filter((arr) => {
            return arr.site === "YouTube" && arr.type === "Teaser";
          });
        }
        //Nu trailerdata c gi tr th ly gi tr   truyn xung cho movie detail, v set Error thnh string rng
        //Nu trailer data khng c th setMovieTrailer thnh mng rng  v bo user l khng c trailer(Ngun Youtube khng c)
        if (trailerdata[0]) {
          setMovieTrailer(trailerdata[0]);
          setError("");
        } else {
          setError("Not found Video");
          setMovieTrailer([]);
        }
      }
    } catch (error) {
      console.log(error);
      setMovieTrailer([]);
      setError(error.message);
    }
  };

  //Xy dng function khi click vo Movie:  nu nh click vo movie c ID trng vi ID ang m th s ng ca s movieDetail
  // Ngc li nu current ID khng bng vi ID va click th s m ca s detail cng nh gi hm getTrailer v set ID
  //C th m movieDetail  multiple th loi phim, cn nu
  const chooseMovieHandler = (event) => {
    if (curMovieID === event.target.id) {
      setMovieModalOpen((prevState) => !prevState);
    } else {
      setMovieModalOpen(true);
    }
    setCurMovieID(event.target.id);
    getTrailer(event.target.id);
  };

  //JSX tr ra tn th loi phim, th div cha tt c cc phim trong th loi nm ngang v dng scrollbar  hin th tt c
  // phn moviedetail hin th theo logic gii thch  trn
  return (
    <div className={styles.container}>
      <h2>{titleList}</h2>
      <div className={styles.listMovieItem} onClick={chooseMovieHandler}>
        {!props.movieListArr.error &&
          props.movieListArr.data.results.map((movie) => (
            <MovieItem
              key={movie.id}
              type={props.movieListArr.type}
              moviesingle={movie}
            />
          ))}
      </div>
      {props.movieListArr.error && (
        <h2 className={styles.errorFetch}>{props.movieListArr.error}</h2>
      )}
      {movieModaleOpen && curMovieID && (
        <MovieDetail
          movieTrailer={movieTrailer}
          error={error}
          moviedetail={props.movieListArr.data.results.filter((movie) => {
            return movie.id === Number(curMovieID);
          })}
        />
      )}
    </div>
  );
};
export default MovieListItem;
