import React, { useState } from "react";
import NavBar from "../../Components/NavBar";
import SearchForm from "../../Components/SearchForm";
import ResultList from "../../Components/ResultList";
import "./Search.css";
const Search = () => {
  //Khai bo cc state  qun l trng thi, data, error, ng m movieDetail, ID movie, trailer  trang Search,
  const [dataSearch, setDataSearch] = useState([]);
  const [isLoading, setIsLoading] = useState(false);
  const [error, setError] = useState(null);
  const [movieModaleOpen, setMovieModalOpen] = useState(false);
  const [curMovieID, setCurMovieID] = useState(0);
  const [movieTrailer, setMovieTrailer] = useState([]);

  const userId = "User 01";
  //Hm  ly data search da trn keywordSearch c lift state t trang SearchForm
  //Nu keyword l rng th tr ra data search l array rng, cn nu li th s tr ra li cho ngi dng
  //Trng thi Loading hin th loading khi bt u search v khi kt thc search
  const getDataSearchMovie = async (dataSearch) => {
    setIsLoading((prevState) => true);
    try {
      const response = await fetch(
        `${process.env.REACT_APP_BACKEND_URL}/api/movies/search?token=${process.env.REACT_APP_USER_TOKEN}&userId=${userId}&page=1`,
        {
          method: "POST",
          mode: "cors",
          headers: { "Content-Type": "application/json" },
          body: JSON.stringify(dataSearch),
        }
      );
      if (!response.ok) {
        const errorMessage = await response.json();
        throw new Error(errorMessage.message);
      }
      const data = await response.json();
      setDataSearch(data.results);
    } catch (error) {
      setDataSearch([]);
      setError(error.message);
    }
    setIsLoading((prevState) => false);
  };
  //Xy dng function ly trailer ca phim
  const getTrailer = async function (movieId) {
    try {
      const response = await fetch(
        `${process.env.REACT_APP_BACKEND_URL}/api/movies/video?token=${process.env.REACT_APP_USER_TOKEN}&userId=${userId}`,
        {
          method: "POST",
          mode: "cors",
          headers: { "Content-Type": "application/json" },
          body: JSON.stringify({ movieId: movieId }),
        }
      );
      //Trong trng hp API b li khng tn ti th bo vi user l phim k c trailer
      if (!response.ok) {
        const errorMessage = await response.json();
        throw new Error(errorMessage.message);
      }
      const data = await response.json();
      //Kt qu ly video  c x l logic  backend
      setMovieTrailer(data[0]);
      setError("");
    } catch (error) {
      setMovieTrailer([]);
      setError(error.message);
    }
  };
  //Xy dng function khi click vo Movie:  nu nh click vo movie c ID trng vi ID ang m th s ng ca s movieDetail
  // Ngc li nu current ID khng bng vi ID va click th s m ca s detail cng nh gi hm getTrailer v set ID
  //C th m movieDetail  multiple th loi phim, cn nu
  const chooseMovieHandler = (event) => {
    if (
      event.target.closest(".movieCheck") &&
      event.target.closest(".movieCheck").classList.contains("movieCheck")
    ) {
      if (curMovieID) {
        setMovieModalOpen(true);
      }
      if (event.target.id) {
        setMovieModalOpen(true);
        getTrailer(event.target.id);
        setCurMovieID(event.target.id);
      }
      if (curMovieID === event.target.id) {
        setMovieModalOpen((prevState) => !prevState);
      }
    } else {
      setMovieModalOpen(false);
    }
  };
  //JSX trang search s tr ra data NavBar, SearchFrom v ResultList
  return (
    <div className="app" onClick={chooseMovieHandler}>
      <NavBar />
      <SearchForm getDataSearchMovie={getDataSearchMovie} />
      <ResultList
        dataSearch={dataSearch}
        isLoading={isLoading}
        error={error}
        movieModaleOpen={movieModaleOpen}
        curMovieID={curMovieID}
        movieTrailer={movieTrailer}
      />
    </div>
  );
};

export default Search;
