import React, { useEffect, useState } from "react";
import NavBar from "../../Components/NavBar";
import Banner from "../../Components/Banner.js";
import MovieList from "../../Components/MovieList";
function Browse() {
  //Cc state  qun l data movie Array, banner random v trng thi loading
  const [movieDataArr, setmovieDataArr] = useState([]);
  const [movieBannerRandom, setMovieBannerRandom] = useState();
  const [isLoading, setIsLoading] = useState(false);
  //API key
  const API_KEY = "381d90cbe832f9ed337e1407f33d8116";

  const userId = "User 01";
  //qun l cc api bng object
  const requests = {
    //Vi nhng link http request khng c param page th khi fetch mc nh thng s page l 1
    fetchNetflixOriginals: `/discover/tv?api_key=${API_KEY}&with_network=123`,
    fetchTrending: `${process.env.REACT_APP_BACKEND_URL}/api/movies/trending?token=${process.env.REACT_APP_USER_TOKEN}&userId=${userId}&page=1`,
    fetchTopRated: `${process.env.REACT_APP_BACKEND_URL}/api/movies/top-rate?token=${process.env.REACT_APP_USER_TOKEN}&userId=${userId}&page=2`,
    fetchActionMovies: `${process.env.REACT_APP_BACKEND_URL}/api/movies/discover?token=${process.env.REACT_APP_USER_TOKEN}&userId=${userId}&genre=Action&page=3`,
    fetchComedyMovies: `${process.env.REACT_APP_BACKEND_URL}/api/movies/discover?token=${process.env.REACT_APP_USER_TOKEN}&userId=${userId}&genre=Comedy&page=4`,
    fetchHorrorMovies: `${process.env.REACT_APP_BACKEND_URL}/api/movies/discover?token=${process.env.REACT_APP_USER_TOKEN}&userId=${userId}&genre=Horror`,
    fetchRomanceMovies: `${process.env.REACT_APP_BACKEND_URL}/api/movies/discover?token=${process.env.REACT_APP_USER_TOKEN}&userId=${userId}&genre=Romance`,
    fetchDocumentaries: `${process.env.REACT_APP_BACKEND_URL}/api/movies/discover?token=${process.env.REACT_APP_USER_TOKEN}&userId=${userId}&genre=Documentary`,
  };
  //Khai bo array rng  sau khi fetching s push cc data ca tng th loi phim vo
  const movieArrTem = [];
  //Hm  fetching data cc th loi phim bng vng lp for trong object
  //Bt u fetching th trng thi loading v true v kt thc s l false  hin th Loading... cho ngi dng
  const getMovieFunction = async function () {
    setIsLoading(true);
    for (const request in requests) {
      //Do l phi fetching t c backend v themoviesdb nn s c ton t 3 ngi  nhn bit fetch t u, cu trc response sau fetch l kh ging nhau
      try {
        const response = await fetch(
          requests[request].slice(0, 4) === "http"
            ? `${requests[request]}`
            : `https://api.themoviedb.org/3${requests[request]}`
        );
        if (!response.ok) {
          const errorMessage = await response.json();
          throw new Error(errorMessage.message);
        }
        const data = await response.json();
        movieArrTem.push({ type: request, data: data });
      } catch (error) {
        movieArrTem.push({ type: request, error: error.message });
      }
    }
    setmovieDataArr(movieArrTem);
    setIsLoading(false);
  };
  //S dng useEffect cho hm gi data movie khi load vo trang web
  useEffect(() => {
    getMovieFunction();
  }, []);
  //Lc ra mng cha data s dng cho phn banner v  Ly random 1 movie cho banner
  useEffect(() => {
    const movieArrforBanner = movieDataArr.filter(
      (dataarr) => dataarr.type === "fetchNetflixOriginals"
    );
    if (movieArrforBanner.length !== 0) {
      setMovieBannerRandom(
        movieArrforBanner[0].data.results[
          Math.floor(
            Math.random() * movieArrforBanner[0].data.results.length - 1
          )
        ]
      );
    }
  }, [movieDataArr]);
  //Thnh phn JSX trang ch gm c Navbar, banner v Movielist cha cc list th loi phim
  return (
    <div className="app">
      <NavBar />
      <Banner movieBannerRandom={movieBannerRandom} isLoading={isLoading} />
      <MovieList movieDataArr={movieDataArr} isLoading={isLoading} />
    </div>
  );
}

export default Browse;
